import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import CommonTable from '../CommonTable'

const columnsPermanent = [
    { id: 'Parameter', label: '–ü–∞—Ä–∞–º–µ—Ç—Ä', minWidth: 170 },    
    { id: 'Value', label: '–ó–Ω–∞—á–µ–Ω–∏–µ', minWidth: 100 },
    { id: 'DateTime', label: '–ü–æ—Å–ª. –∏–∑–º–µ–Ω–µ–Ω–∏–µ', minWidth: 100 },
  ];

const columnsVolatile = [
    { id: 'EquipName', label: '–ö–æ–º–ø–ª–µ–∫—Å', minWidth: 170 },
    { id: 'DateTime', label: '–í—Ä–µ–º—è', minWidth: 100 },
    { id: 'Processor_CPU_Load', label: 'CPU –∑–∞–≥—Ä—É–∑–∫–∞', minWidth: 100 },
    { id: 'Memory_Memory_free_Gb', label: '–°–≤–æ–±–æ–¥–Ω–æ –ø–∞–º—è—Ç–∏', minWidth: 100 },
    { id: 'HddName', label: '–ò–º—è –¥–∏—Å–∫–∞', minWidth: 100 },
    { id: 'HddFreeSpace', label: '–°–≤–æ–±–æ–¥–Ω–æ', minWidth: 100 },  
  ];

  const useStyles = makeStyles({
    root: {
      width: '100%',
    }
  });
  
export default function SystemTable(props) {
  console.log("render SystemTable");

  const classes = useStyles();
  const volatileInfoRows = props.data?.VolatileInfo;
  const permanentInfo = props.data?.PermanentInfo?.length > 0 ? props.data?.PermanentInfo[0] : null;
  const hdds = permanentInfo?.HDD?.map(e => 
    { 
      return { Parameter: `–õ–æ–≥–∏—á–µ—Å–∫–∏–π ${e.Letter}`, Value: e.TotalSize, DateTime: permanentInfo?.DateTime }
    });
  
  const physicalDisks = permanentInfo?.PhysicalDisks?.map(e => 
    { 
      return { Parameter: `–§–∏–∑–∏—á–µ—Å–∫–∏–π ${e.MediaType} ${e.FriendlyName}`, Value: e.Size_Gb, DateTime: permanentInfo?.DateTime }
    });

  const monitors = permanentInfo?.Monitor?.map(e => 
    { 
      return { Parameter: `–ú–æ–Ω–∏—Ç–æ—Ä ${e.Device_Name}`, Value: `${e.Width}x${e.Height}`, DateTime: permanentInfo?.DateTime }
    });

  const vga = permanentInfo?.VGA?.map(e => 
    { 
      return { Parameter: `–í–∏–¥–µ–æ–∞–¥–∞–ø—Ç–µ—Ä ${e.Card_Name}`, Value: `${e.Memory_Gb}`, DateTime: permanentInfo?.DateTime }
    });

  const processor = permanentInfo?.Processor?.Model ? [ { Parameter: "–ü—Ä–æ—Ü–µ—Å—Å–æ—Ä", Value: permanentInfo?.Processor.Model, DateTime: permanentInfo?.DateTime }] : []
  const motherboard = permanentInfo?.Motherboard?.Model ? [ { Parameter: "–ú–∞—Ç–µ—Ä–∏–Ω—Å–∫–∞—è –ø–ª–∞—Ç–∞", Value: permanentInfo?.Motherboard.Model, DateTime: permanentInfo?.DateTime }] : []
  const memory = permanentInfo?.Memory?.Memory_total_Gb ? [ { Parameter: "–ü–∞–º—è—Ç—å, –ì–±", Value: permanentInfo?.Memory.Memory_total_Gb, DateTime: permanentInfo?.DateTime }] : []

  const permanentInfoRows = 
    processor
    .concat(memory)
    .concat(hdds ?? [])
    .concat(motherboard)    
    .concat(physicalDisks ?? [])    
    .concat(monitors ?? [])
    .concat(vga ?? []);

  return (
    <div className={classes.root}>
      |{props.equipName ? 
        <div>
          <Typography variant="h5" component="h2">
              {props.equipName}
          </Typography>
          <CommonTable columns={columnsPermanent} rows={permanentInfoRows}></CommonTable>
          </div>
        : <></>
      }
      <CommonTable columns={columnsVolatile} rows={volatileInfoRows}></CommonTable>
    </div>
  );
}