import React, {useContext}  from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import { CurrentEquipContext } from '../../context/currentEquip-context';
import {useCardsStyles} from './CommonCard'
import CardRow from './CardRow'

// export default function DetectorCard(props) {
const DetectorCard = React.memo((props) => {
  console.log(`! render DetectorCard`);

  const classes = useCardsStyles();
  const bull = <span className={classes.bullet}>‚Ä¢</span>;

  const detectors = props.detectors;
  const aecs = props.aecs;
  return (
    <Card className={classes.root}>
      <CardContent>
        <Typography variant="h5" component="h2">
          {bull}–î–µ—Ç–µ–∫—Ç–æ—Ä—ã
        </Typography>
        {detectors?.map((i, ind) => (
          <div key={ind.toString()} >
            <Typography variant="h6" component="h2">
              {bull}{i.DetectorName} 
            </Typography>
            <CardRow descr="–°–æ—Å—Ç–æ—è–Ω–∏–µ" 
              value={i.State !== 2? "–ù–µ –≥–æ—Ç–æ–≤" : "–ì–æ—Ç–æ–≤"}
              rightColor={i.State !== 2? "red" : "green"}></CardRow>
            <CardRow descr="–ü–æ–ª–µ –¥–µ—Ç–µ–∫—Ç–æ—Ä–∞" value={i.DetectorField}></CardRow>
            {aecs?
              <>
                <Typography variant="h6" component="h2">
                  {bull}–ê–ö–≠
                </Typography>
                <CardRow descr="–°–æ—Å—Ç–æ—è–Ω–∏–µ" value={aecs.State?.Aec_enabled ? "–í–∫–ª" : "–í—ã–∫–ª"}></CardRow>
                <CardRow descr="–ü–æ–ª—è –ê–ö–≠" value={aecs.State?.Aec_fields}></CardRow>
                <CardRow descr="–ü–ª–æ—Ç–Ω–æ—Å—Ç—å" value={aecs.State?.Aec_density}></CardRow>
              </> : <></>
            }
          </div>
          ))
        }        
      </CardContent>
    </Card>
  );
});

export default DetectorCard;