import React, {useContext} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import { CurrentEquipContext } from '../../context/currentEquip-context';
import {useCardsStyles} from './CommonCard'
import CardRow from './CardRow'

const SystemCard = React.memo((props) => {
//export default function SystemCard() {
  console.log(`! render SystemCard`);

  const classes = useCardsStyles();
  const bull = <span className={classes.bullet}>‚Ä¢</span>;

  const system = props.system;
  const volatile = props.volatile;
  const logicalDisks = system?.LogicalDisks; // HDD
  const physicalDisks = system?.HardDrives;  // PhysicalDisks
  const lans = system?.Lans; // Network
  const vgas = system?.VideoAdapters; // VGA
  const monitors = system?.Monitors; // Monitor
  const printers = system?.Printers; //Printer
  const processor = system?.Motherboards ? system?.Motherboards[0] : undefined; // Processor
  //
  return (
    <Card className={classes.root}>
      <CardContent>
        <Typography variant="h5" component="h2">
          {bull}CPU
        </Typography>
        {processor?.Cpu || volatile?.Processor?.CPULoad || volatile?.Processor?.CPULoad === 0? 
          <>
            <CardRow descr="–ú–æ–¥–µ–ª—å" value={processor?.Cpu} rightWidth={'100%'}></CardRow>
            <CardRow descr="–ó–∞–≥—Ä—É–∑–∫–∞" value={volatile?.Processor?.CPULoad+'%'}></CardRow>
          </> : <></>}

        <Typography variant="h5" component="h2">
          {bull}–ü–∞–º—è—Ç—å
        </Typography>
        {volatile?.Memory?.MemoryTotalGb || volatile?.Memory?.MemoryFreeGb ? 
          <>
            <CardRow descr="–í—Å–µ–≥–æ" value={volatile?.Memory?.MemoryTotalGb+' –ì–±'}></CardRow>
            <CardRow descr="–î–æ—Å—Ç—É–ø–Ω–æ" value={volatile?.Memory?.MemoryFreeGb+' –ì–±'}></CardRow>
          </> : <></>}

        <Typography variant="h5" component="h2">
          {bull}–î–∏—Å–∫–∏
        </Typography>
        <Typography variant="h6" component="h2">
              {bull}–õ–æ–≥–∏—á–µ—Å–∫–∏–µ –¥–∏—Å–∫–∏
        </Typography>
        {logicalDisks?.map((i, ind) => (
          <div key={ind.toString()} >
            <Typography variant="h6" component="h2">
              {bull}–î–∏—Å–∫ {i.Letter}
            </Typography>
            <CardRow descr="–í—Å–µ–≥–æ" value={i.TotalSize+' –ì–±'}></CardRow>
            <CardRow descr="–î–æ—Å—Ç—É–ø–Ω–æ" value={i.FreeSize+' –ì–±'}></CardRow>
          </div>
          ))
        } 
        <Typography variant="h6" component="h2">
              {bull}–§–∏–∑–∏—á–µ—Å–∫–∏–µ –¥–∏—Å–∫–∏
        </Typography>
        {physicalDisks?.map((i, ind) => (
          <div key={ind.toString()} >
            <Typography variant="h6" component="h2">
              {bull}–î–∏—Å–∫ {i.mediatype}
            </Typography>
            <CardRow descr={i.FriendlyName+' –ì–±'} value={i.SizeGb+' –ì–±'}></CardRow>
          </div>
          ))
        } 

        <Typography variant="h5" component="h2">
          {bull}–°–µ—Ç—å
        </Typography>
        {lans?.filter(i => i.Adapter).map((i, ind) => (
          <CardRow key={ind.toString()} descr={i.Adapter} value={i.Ip}></CardRow>
          ))
        } 

        <Typography variant="h5" component="h2">
          {bull}–í–∏–¥–µ–æ–∞–¥–∞–ø—Ç–µ—Ä—ã
        </Typography>
        {vgas?.map((i, ind) => (
          <div key={ind.toString()} >
            <CardRow descr="–ò–º—è" value={i.CardName} rightWidth={'100%'}></CardRow>
            <CardRow descr="–ü–∞–º—è—Ç—å" value={i.MemoryGb+' –ì–±'}></CardRow>
            <CardRow descr="–î—Ä–∞–π–≤–µ—Ä" value={i.DrvDate} rightWidth={'100%'}></CardRow>
          </div>
          ))
        } 

        <Typography variant="h5" component="h2">
          {bull}–ú–æ–Ω–∏—Ç–æ—Ä—ã
        </Typography>
        {monitors?.map((i, ind) => (
          // <CardRow key={ind.toString()} descr={i.MonitorName} value={i.Width+'x'+i.Height}></CardRow>
          <CardRow key={ind.toString()} descr={i.MonitorName} value={i.SerialNumber} rightWidth={'100%'}></CardRow>
          ))
        } 

        <Typography variant="h5" component="h2">
          {bull}–ü—Ä–∏–Ω—Ç–µ—Ä—ã
        </Typography>
        {printers?.map((i, ind) => (
          <div key={ind.toString()} >
            <CardRow descr="–ò–º—è" value={i.PrinterName} rightWidth={'100%'}></CardRow>
            <CardRow descr="–ü–æ—Ä—Ç" value={i.PrinterPort} rightWidth={'100%'}></CardRow>
          </div>
          ))
        } 
      </CardContent>
    </Card>
  );
});

export default SystemCard;